.main {
    padding: 10px 20px;
    flex-grow: 1;
    width: 900px;
    margin: 0 auto;

    a {
        color: black;
    }
}
@media (max-width: 800px) {
   .main {
       width: 1vh;
   }
}

.btn {
    background-color: transparent;
    border: 1px solid #6464d4 !important;
    border-radius: 5px !important;
    padding: 6px 17px;
    color: #6464d4 !important;
    margin: 0;
    cursor: pointer;
    text-transform: capitalize !important;
}